import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownFilterProps } from './DropDownFilter.interface';
import React, { Fragment } from 'react';
import { getDistanceToBottom } from '../../../utils/getDistanceToBottom';
import { Combobox, Transition } from '@headlessui/react';
import { twMerge } from 'tailwind-merge';

const MENU_HEIGHT = 268;

const highlightQuery = (string: string, query: string) => {
  const regex = new RegExp(query, 'gi');
  return string.replace(regex, (match) => '<b>' + match + '</b>');
};

const getOptionItemClasses = (hovered: boolean, isActive: boolean) => {
  return clsx(
    // TODO: replace zep-px-1 with `zep-px-dropdown-option-px` , zep-py-0.75 with `zep-py-dropdown-option-py`
    // TODO: add 'zep-bg-dropdown-option-bg-default' as default bg color
    'zep-flex zep-w-full zep-items-center zep-justify-between zep-px-1 zep-py-0.75',
    // TODO: 'zep-bg-dropdown-bg-readOnly' replace with 'zep-bg-dropdown-option-bg-hover'
    { 'zep-bg-dropdown-bg-readOnly/70': hovered },
    // TODO: 'zep-bg-dropdown-bg-readOnly' replace with 'zep-bg-dropdown-option-bg-active'
    { 'zep-bg-dropdown-bg-readOnly': isActive },
    // TODO: replace 'zep-pr-3' with 'zep-py-dropdown-option-pr-active'
    { 'zep-pr-3': isActive },
  );
};

export const DropDownFilter: React.FC<DropDownFilterProps> = ({
  disabled = false,
  items,
  label,
  message,
  noOptionsLabel,
  placeholder,
  readonly = false,
  state = 'default',
  ...props
}) => {
  const menuRef = React.useRef(null);
  const inputRef = React.useRef(null);
  const hasError = state === 'error';
  const [query, setQuery] = React.useState('');
  const [selectedPerson, setSelectedPerson] = React.useState(null);
  const hasInput = query !== '' || selectedPerson !== null;
  const disabledOrReadonly = disabled || readonly;
  const iconColor = hasError
    ? 'zep-text-dropdown-supportText-error'
    : state === 'info'
    ? 'zep-text-dropdown-supportText-info'
    : 'zep-text-dropdown-supportText-default';

  const filteredItems =
    query === ''
      ? items
      : items.filter((item) => {
          return item.name.toLowerCase().includes(query.toLowerCase());
        });

  return (
    <div
      // TODO: use `zep-opacity-dropdown-disabled` here && remove all other disabled styles from input, options, icon etc.
      className={twMerge(clsx('zep-relative', 'zep-inline-block', 'zep-text-left', 'zep-w-[326px]'), props.className)}
      data-testid="zep-drop-down-filter"
      {...props}
    >
      <Combobox as="div" value={selectedPerson} onChange={setSelectedPerson} disabled={disabledOrReadonly}>
        {({ open }) => {
          const distanceToBottom = getDistanceToBottom(inputRef.current);
          const openOnTop = distanceToBottom < MENU_HEIGHT;
          const color = disabled
            ? 'zep-text-dropdown-placeholder-default/40'
            : !hasInput && !readonly
            ? 'zep-text-dropdown-placeholder-default/70'
            : 'zep-text-dropdown-placeholder-default';

          const _placeholder = disabled
            ? 'zep-text-dropdown-placeholder-default/40'
            : !readonly
            ? 'zep-text-dropdown-placeholder-default'
            : 'zep-text-dropdown-placeholder-default/70';

          const inputClasses = clsx(
            'zep-inline-flex',
            'zep-justify-between',
            'zep-rounded-dropdown',
            'zep-border-solid',
            'zep-pl-1', // TODO: -> zep-pl-dropdown-pl
            'zep-py-0.75', // TODO: -> zep-py-dropdown-py
            'zep-font-roboto',
            'zep-w-full',
            'zep-outline-none',
            'zep-truncate',
            hasError ? 'zep-border-2' : 'zep-border-1', // TODO: should be zep-border-dropdown-width-error &  zep-border-dropdown-width-default
            hasInput ? 'zep-pr-4' : 'zep-pr-0.75', // TODO: -> zep-pr-dropdown-pr
            color,
            _placeholder,
            { 'zep-ring': open },
            { 'zep-bg-dropdown-bg-readOnly': readonly },
            { 'zep-border-dropdown-default': !disabled && !hasError },
            { 'border-zep-typography-dark-70/40': disabled },
            { 'zep-border-error': hasError },
            { 'zep-active:ring': !disabledOrReadonly },
            { 'zep-ring-offset-1': !disabledOrReadonly },
            { 'focus-visible:ring': !disabledOrReadonly },
            { 'zep-ring-focus': !disabledOrReadonly },
            { 'zep-cursor-not-allowed': disabledOrReadonly },
          );

          const optionContainerClasses = clsx(
            'zep-absolute',
            'zep-py-0.25', // TODO: 'zep-py-dropdown-optionsContainer-py
            'zep-z-10',
            'zep-right-0',
            'zep-w-[326px]', // TODO: 'zep-min-w-dropdown'
            'zep-max-h-[268px]', // TODO: 'zep-max-h-dropdown-optionsContainer'
            'zep-origin-top-right',
            'zep-rounded-dropdown',
            'zep-bg-dropdown-bg-default', // TODO:  replace with zep-shadow-dropdown-optionsContainer-default
            'zep-drop-shadow-sm',
            'zep-ring-1,',
            'zep-ring-tertiary',
            'zep-ring-opacity-1',
            'focus:outline-none',
            'zep-overflow-hidden',
            'zep-overflow-y-auto',
            openOnTop ? 'zep-mb-[-25px]' : 'zep-mt-0.25', // TODO: 'zep-mt-dropdown-optionsContainer'
            { 'zep-bottom-full': openOnTop },
          );

          const deleteBtnClasses = clsx(
            'zep-absolute',
            'zep-top-0.75',
            'zep-right-2.5',
            'zep-flex',
            'zep-items-center',
          );

          const iconClasses = clsx(
            disabledOrReadonly ? 'zep-text-dropdown-placeholder-default/40' : 'zep-text-dropdown-placeholder-default',
            'zep-transition-transform',
            'zep-duration-500',
            'zep-shrink-0',
            {
              'zep-rotate-180': open,
            },
          );

          return (
            <>
              {label && (
                <Combobox.Label
                  className={clsx('zep-block', 'zep-mb-0.5', { 'text-zep-typography-dark-70/40': disabled })} // TODO: remove after using `zep-opacity-disabled` on parent
                >
                  {label}
                </Combobox.Label>
              )}
              <div className="zep-relative zep-mt-0.5">
                <Combobox.Input
                  className={inputClasses}
                  autoComplete="off"
                  placeholder={placeholder}
                  ref={inputRef}
                  data-testid="zep-drop-down-filter-input"
                  onChange={(event) => setQuery(event.target.value)}
                />
                {Boolean(selectedPerson) && (
                  <button
                    className={deleteBtnClasses}
                    data-testid="zep-drop-down-filter-delete"
                    disabled={disabledOrReadonly}
                    onClick={() => {
                      setQuery('');
                      setSelectedPerson(null);
                    }}
                  >
                    <FunctionalIcon className={color} name="DeleteCircle" size={24} />
                  </button>
                )}
                <Combobox.Button className="zep-absolute zep-inset-y-[0px] zep-right-[0px] zep-flex zep-items-center zep-pr-0.5">
                  <FunctionalIcon className={iconClasses} name="ChevronMiniDown" role="img" size={24} />
                </Combobox.Button>
                <Transition
                  as={Fragment}
                  enter="zep-transition zep-ease-out zep-duration-100"
                  enterFrom="zep-transform zep-opacity-0 zep-scale-95"
                  enterTo="zep-transform zep-opacity-100 zep-scale-100"
                  leave="zep-transition zep-ease-in zep-duration-25"
                  leaveFrom="zep-transform zep-opacity-100 zep-scale-100"
                  leaveTo="zep-transform zep-opacity-0 zep-scale-95"
                >
                  {filteredItems.length > 0 ? (
                    <Combobox.Options
                      className={optionContainerClasses}
                      ref={menuRef}
                      data-testid="zep-drop-down-filter-menu"
                    >
                      {filteredItems.map((item) => (
                        <Combobox.Option
                          key={item.id}
                          data-testid="zep-drop-down-filter-option"
                          value={item.name}
                          className={({ active, selected }) => getOptionItemClasses(active, selected)}
                        >
                          {({ selected }) => (
                            <>
                              <span
                                className="zep-truncate"
                                dangerouslySetInnerHTML={{ __html: highlightQuery(item.name, query) }}
                              />
                              {selected && (
                                <FunctionalIcon
                                  name="Check"
                                  role="img"
                                  size={24}
                                  className="zep-absolute zep-right-1"
                                />
                              )}
                            </>
                          )}
                        </Combobox.Option>
                      ))}
                    </Combobox.Options>
                  ) : (
                    <Combobox.Options className={optionContainerClasses} ref={menuRef}>
                      <Combobox.Option
                        data-testid="zep-drop-down-filter-no-options"
                        value={0}
                        className={clsx('zep-block zep-py-0.75 zep-text-center')}
                      >
                        {noOptionsLabel}
                      </Combobox.Option>
                    </Combobox.Options>
                  )}
                </Transition>
              </div>
            </>
          );
        }}
      </Combobox>
      {message && (
        <div
          className={clsx(
            'zep-mt-[6px]', // TODO: 'zep-mt-dropdown-support-gap'
            'zep-tagline',
            'zep-text-0.875', // TODO: 'zep-text-dropdown-support'
            'zep-flex',
            'zep-items-center',
            iconColor,
          )}
        >
          {state !== 'default' && (
            <FunctionalIcon
              name={state === 'error' ? 'Warning' : 'Info'}
              role="img"
              size={24}
              // TODO: replace with zep-gap-dropdown-support-gap on parent
              className="zep-mr-0.5 zep-shrink-0"
            />
          )}
          <span>{message}</span>
        </div>
      )}
    </div>
  );
};
